import { extendTheme } from "@chakra-ui/react";

const config = {
  initialColorMode: "system",
  useSystemColorMode: true,
};

function darkenColor(hexColor, amount = 0.1) {
  const hex = hexColor.replace("#", "");
  const bigint = parseInt(hex, 16);
  let r = (bigint >> 16) & 255;
  let g = (bigint >> 8) & 255;
  let b = bigint & 255;
  r = Math.max(0, Math.min(255, Math.round(r * (1 - amount))));
  g = Math.max(0, Math.min(255, Math.round(g * (1 - amount))));
  b = Math.max(0, Math.min(255, Math.round(b * (1 - amount))));
  return `#${((r << 16) | (g << 8) | b).toString(16).padStart(6, "0")}`;
}

function lightenColor(hexColor, amount = 0.1) {
  const hex = hexColor.replace("#", "");
  const bigint = parseInt(hex, 16);
  let r = (bigint >> 16) & 255;
  let g = (bigint >> 8) & 255;
  let b = bigint & 255;
  r = Math.max(0, Math.min(255, Math.round(r + (255 - r) * amount)));
  g = Math.max(0, Math.min(255, Math.round(b + (255 - b) * amount)));
  b = Math.max(0, Math.min(255, Math.round(b + (255 - b) * amount)));
  return `#${((r << 16) | (g << 8) | b).toString(16).padStart(6, "0")}`;
}

const catppuccinLight = {
  base: "#eff1f5",
  textPrimary: "#4c4f69",
  primaryButton: "#dd7878",
  accent: "#ea9a97",
  secondary: "#56949f",
  background: "#faf4ed",
};

const catppuccinDark = {
  base: "#363a4f",
  textPrimary: "#f4dbd6",
  primaryButton: "#ed8796",
  accent: "#f4b8b8",
  secondary: "#8aadf4",
  background: "#24273a",
};

const sectionColors = {
  light: {
    hero: "#f5e0dc",
    features: "#eff1f5",
    community: "#f4dbd6",
  },
  dark: {
    hero: "#363a4f",
    features: "#24273a",
    community: "#494d64",
  },
};

const colors = {
  light: {
    ...catppuccinLight,
    ...sectionColors.light,
    buttonHover: {
      primary: lightenColor(catppuccinLight.primaryButton, 0.1),
    },
  },
  dark: {
    ...catppuccinDark,
    ...sectionColors.dark,
    buttonHover: {
      primary: lightenColor(catppuccinDark.primaryButton, 0.1),
    },
  },
};

const styles = {
  global: (props) => ({
    body: {
      bg:
        props.colorMode === "light"
          ? colors.light.background
          : colors.dark.background,
      color:
        props.colorMode === "light"
          ? colors.light.textPrimary
          : colors.dark.textPrimary,
    },
  }),
};

const components = {
  Button: {
    baseStyle: {
      fontWeight: "bold",
    },
    sizes: {
      md: {
        height: "48px",
        fontSize: "lg",
        px: "4",
      },
    },
    variants: {
      solid: (props) => ({
        bg:
          props.colorMode === "light"
            ? colors.light.primaryButton
            : colors.dark.primaryButton,
        color:
          props.colorMode === "light"
            ? colors.light.textPrimary
            : colors.dark.textPrimary,
        _hover: {
          bg:
            props.colorMode === "light"
              ? colors.light.buttonHover.primary
              : colors.dark.buttonHover.primary,
        },
      }),
    },
  },
};

const theme = extendTheme({ config, styles, colors, components });

export default theme;
